.hero{
    display : flex;
    height : 100vh;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
}
.hero-left{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content : center;
    gap : 20px;
    padding-left : 180px;
}
.hero-left h2{
    font-size: 20px;
    font-family :'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
}
.hero-left p{
    color : #171717;
    font-size : 50px;
    font-weight : 700;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap : 20px;
}
.hero-hand-icon img{

    width : 105px;
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap : 15px;
    width : 310px;
    height : 70px;
    border-radius: 75px;
    margin-top : 30px;
    background: #ff4141;
    color : white;
    font-size: 22px;
    font-weight: 500;
}

