.shopcategory-banner{
    display : block;
    margin : 30px auto;
    width : 82%;
}
.shopcategory-indexSort{
    display: flex;
    margin : 0px 170px;
    justify-content: space-between;
    align-items: center;
}
.shopcategory-sort{
    padding : 10px 20px;
    border-radius : 40px;
    border : 1px solid #888;
    cursor: pointer;
}
.shopcategory-indexSort span{
    font-weight : 600;
}
.shopcategory-products{
    display : grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin : 20px 50px;
    row-gap: 80px;
}
.shopcategory-loadmore{
    display : flex;
    justify-content: center;
    align-items: center;
    margin : 150px auto;
    width : 230px;
    height : 69px;
    border-radius : 75px;
    background : #ededed;
    color : #787878;
    font-size : 18px;
    font-weight : 500;
    cursor: pointer;
}